.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 0px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.menu-item{min-width:30%;height:350px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:350px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{height:90px;padding:0 20px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.6;position:absolute}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:18px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:14px}

/*google font*/

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Poppins',sans-serif;
}
/*slider*/
#carouselExampleIndicators{
	position: relative;
	height: 100vh;
	width: 100%;
}
#carouselExampleIndicators .carousel-inner{
	position: relative;
	height: 100%;
	width: 100%;
	background: #000;
}
#carouselExampleIndicators .carousel-inner:before{
	position: absolute;
	content: '';
	background: rgba(0, 0, 0, 0.45);
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	z-index: 1;
}
#carouselExampleIndicators .carousel-inner .carousel-item{
	position: relative;
	height: 100%;
	width:100%;
	background: url(/EcomSite/static/media/sneakers.be91b575.jpg);
	background-size: 100% 100%;
	background-position: center;
	background-attachment: fixed;
}
#carouselExampleIndicators .carousel-inner .carousel-item:nth-child(2){
	background: url(/EcomSite/static/media/women.d7c263d2.jpg);
	background-size: 100% 150%;
	background-position: top;
	background-attachment: fixed;
}
#carouselExampleIndicators .carousel-inner .carousel-item:nth-child(3){
	background: url(/EcomSite/static/media/mens.db9eba89.jpg);
	background-size: 100% 100%;
	background-position: center;
	background-attachment: fixed;
}

#carouselExampleIndicators .carousel-inner .carousel-item .info{
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	height: auto;
	width: 100%;
	z-index: 1;
	text-align: center;
}
#carouselExampleIndicators .carousel-inner .carousel-item h1{
	height: auto;
	color: #fff;
	width: 100%;
	z-index: 1;
	text-align: center;
	font-size: 5.5em;
	font-weight: 750;
}
#carouselExampleIndicators .carousel-inner .carousel-item p{
	height: auto;
	color: #ccc;
	width: 100%;
	z-index: 1;
	text-align: center;
	font-size: 1.2em;
	font-weight: 500;
}
.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#86a8df;color:white}.custom-button.google-sign-in:hover{background-color:#00008b;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.collection-item{width:22%;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:flex}.collection-item .collection-footer{width:100%;height:5%;display:flex;flex-direction:row;justify-content:space-between;font-size:15px}.collection-item .collection-footer .name{width:90%}.collection-item .collection-footer .price{width:10%}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px;padding:25px 10px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}.header .options a{color:black}.header .options a:hover{background-color:black;color:white;opacity:0.6;text-decoration:none}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:16px;margin:50px auto}.cart-dropdown .cartitems{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{position:absolute;bottom:20px}


